#main-container-sponsors{ 
    /* border-color: red;
    border: solid; */
    display: flex;
    flex-direction: column;
    margin-bottom: 2.5%;
    padding-top: 10%;
}

#main-container-sponsors h1{
    font-size: 60px;
    justify-content: center;
    /* border: solid; */
    justify-items: center;
    display: flex;
    color: rgb(202, 232, 255);
    margin: 0;
    margin-bottom: 2.5%;

    animation-name: pulse-white-glow-b;
    animation-delay: 0ms;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count:infinite;
    animation-direction: alternate;
    animation-timing-function: linear;
}

#sponsors-container{
    /* color: yellow;
    border: solid; */
    display: flex;
    width: 100vw;
    flex-wrap: wrap;
    justify-content: center;
}

.sponsor-image{
    /* border: solid;
    color: white; */
    height: 37.5vh;
    width: auto;
    margin: 0.5%;
    
}

@media (max-width: 768px){
    #main-container-sponsors h1{
        font-size: 40px;
    }

    .sponsor-image{
        /* border: solid;
        color: white; */
        height: 15vh;
        width: auto;
        margin: 0.5%;
        
    }
}